import React from 'react';
import { useTranslation } from "react-i18next";

export function Adventages() {
    const { t } = useTranslation();

    const advantagesItemsData = [
        {
            img: '/images/icon-1.png',
            title: t("advantages.block-1.title"),
            text: t("advantages.block-1.text")
        },
        {
            img: '/images/icon-2.png',
            title: t("advantages.block-2.title"),
            text: t("advantages.block-2.text")
        },
        {
            img: '/images/icon-3.png',
            title: t("advantages.block-3.title"),
            text: t("advantages.block-3.text")
        },
    ];

    return (
        <section className="advantages">
            <div className="container advantages__container">
                {advantagesItemsData.map((item, index) => (
                    <div key={index} className="advantages__item">
                        <img src={item.img} alt="some-img" className="advantages__img" />
                        <div className="advantages__content">
                            <h2 className="advantages__title gradient">{item.title}</h2>
                            <p className="advantages__descr">{item.text}</p>
                        </div>
                    </div>
                ))}
            </div>
        </section>
    )
}
