import { useTranslation } from "react-i18next";

export function Features() {
    const { t } = useTranslation();

    const featuresItemsData = [
        {
            title: t("features.block-1.title"),
            text: t("features.block-1.text")
        },
        {
            title: t("features.block-2.title"),
            text: t("features.block-2.text")
        },
        {
            title: t("features.block-3.title"),
            text: t("features.block-3.text")
        },
        {
            title: t("features.block-4.title"),
            text: t("features.block-4.text")
        },
    ];

    return (
        <section className="features">
            <div className="features__container container">
                <h2 className="features__title">{t("features.title")}</h2>
                <div className="features__wrapper">
                    {featuresItemsData.map((item, index) => (
                        <div key={index} className="features__item">
                            <h3 className="features__caption">{item.title}</h3>
                            <p className="features__text">{item.text}</p>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}