import React from 'react';
import { useTranslation } from "react-i18next";

export function Multi() {
    const { t } = useTranslation();

    const multiItemsData = [
        {
            title: t("multi.block-1.title"),
            text: t("multi.block-1.text")
        },
        {
            title: t("multi.block-2.title"),
            text: t("multi.block-2.text")
        },
        {
            title: t("multi.block-3.title"),
            text: t("multi.block-3.text")
        },
    ];

    return (
        <section className="multi">
            <div className="container multi__container">
                <h2 className="multi__title">{t("multi.title")}</h2>
                <p className="multi__descr">{t("multi.description")}</p>
                <div className='multi__wrapper'>
                    <div className="multi__left">
                        {multiItemsData.map((item, index) => (
                            <div key={index} className="multi__item">
                                <div className="multi__content">
                                    <h2 className="multi__caption">{item.title}</h2>
                                    <p className="multi__text">{item.text}</p>
                                </div>
                            </div>
                        ))}
                    </div>
                    <img src="/images/components-img-1.png" alt="" className='multi__img' />
                </div>
            </div>
        </section>
    )
}