
import { useTranslation } from "react-i18next";


export function LastBlock() {
    const { t } = useTranslation();

    return (
        <section className="download">
            <div className="download__container container">
                <h2 className="download__title gradient">{t("download.title")}</h2>
                <div className="download__buttons">
                    <button className="download__appstore"><img src="/images/app_store.svg" alt="download__appstore" /></button>
                    <button className="download__googleplay"><img src="/images/google_play.svg" alt="googleplay" /></button>
                </div>
                <img src="/images/download-img.png" alt="" className="download__img" />
            </div>
        </section>
    );
}